"use client"

import { useEffect, useState, useRef } from "react"
import { Sidebar } from "@/components/sidebar"
import { Header } from "@/components/header"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Badge } from "@/components/ui/badge"
import { Switch } from "@/components/ui/switch"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { emailsAPI, contactsAPI, opportunitiesAPI, communicationsAPI } from "@/lib/api"
import {
  isEmailRead,
  markEmailAsRead,
  markEmailAsUnread,
  markAllAsRead,
  initializeEmailStatus,
  getUnreadCount,
  getAutosyncInterval,
  setAutosyncInterval,
  isAutosyncEnabled,
  setAutosyncEnabled,
} from "@/lib/email-status"
import { Mail, Search, Filter, ExternalLink, Link2, RefreshCw, Loader2, Inbox as InboxIcon, Archive, Star, MailOpen, Circle, CheckCircle2 } from "lucide-react"
import { format, formatDistanceToNow } from "date-fns"
import Link from "next/link"

interface Email {
  id: number
  type: string
  subject: string
  body: string
  from_email: string
  to_email: string
  contact_id: number | null
  opportunity_id: number | null
  external_id: string
  source: string
  conversation_id: string | null
  message_id: string | null
  deep_link: string | null
  occurred_at: string
  created_at: string
  contact_name: string | null
  contact_email: string | null
}

interface Contact {
  id: string
  name: string
  email: string
}

interface Opportunity {
  id: string
  title: string
}

export default function InboxPage() {
  const [emails, setEmails] = useState<Email[]>([])
  const [selectedEmail, setSelectedEmail] = useState<Email | null>(null)
  const [loading, setLoading] = useState(true)
  const [syncing, setSyncing] = useState(false)
  const [searchQuery, setSearchQuery] = useState("")
  const [filterContact, setFilterContact] = useState<string | null>(null)
  const [filterOpportunity, setFilterOpportunity] = useState<string | null>(null)
  const [filterUnread, setFilterUnread] = useState(false)
  const [contacts, setContacts] = useState<Record<string, Contact>>({})
  const [opportunities, setOpportunities] = useState<Record<string, Opportunity>>({})
  const [showLinkDialog, setShowLinkDialog] = useState(false)
  const [linkingEmailId, setLinkingEmailId] = useState<number | null>(null)
  const [autosyncEnabled, setAutosyncEnabledState] = useState(isAutosyncEnabled())
  const [autosyncInterval, setAutosyncIntervalState] = useState(getAutosyncInterval())
  const autosyncTimerRef = useRef<NodeJS.Timeout | null>(null)

  useEffect(() => {
    loadEmails()
    loadContacts()
    loadOpportunities()
  }, [filterContact, filterOpportunity])

  // Initialize email status for new emails
  useEffect(() => {
    if (emails.length > 0) {
      initializeEmailStatus(emails.map((e) => e.id))
    }
  }, [emails])

  // Autosync setup
  useEffect(() => {
    if (autosyncEnabled) {
      const interval = autosyncInterval
      const sync = async () => {
        try {
          await emailsAPI.sync()
          await loadEmails()
        } catch (error) {
          console.error("Autosync error:", error)
        }
      }

      // Initial sync after interval
      autosyncTimerRef.current = setInterval(sync, interval)

      return () => {
        if (autosyncTimerRef.current) {
          clearInterval(autosyncTimerRef.current)
        }
      }
    } else {
      if (autosyncTimerRef.current) {
        clearInterval(autosyncTimerRef.current)
        autosyncTimerRef.current = null
      }
    }
  }, [autosyncEnabled, autosyncInterval])

  // Mark email as read when selected
  useEffect(() => {
    if (selectedEmail && !isEmailRead(selectedEmail.id)) {
      markEmailAsRead(selectedEmail.id)
      // Force re-render to update UI
      setEmails([...emails])
    }
  }, [selectedEmail])

  const loadEmails = async () => {
    try {
      setLoading(true)
      const params: any = { limit: 100 }
      if (filterContact) params.contactId = filterContact
      if (filterOpportunity) params.opportunityId = filterOpportunity
      
      const response = await emailsAPI.getAll(params)
      const loadedEmails = response.data || []
      setEmails(loadedEmails)
      
      // Initialize status for new emails (mark as unread)
      if (loadedEmails.length > 0) {
        initializeEmailStatus(loadedEmails.map((e: Email) => e.id))
      }
    } catch (error: any) {
      console.error("Error loading emails:", error)
    } finally {
      setLoading(false)
    }
  }

  const loadContacts = async () => {
    try {
      const response = await contactsAPI.getAll()
      const contactsMap: Record<string, Contact> = {}
      response.data.forEach((c: any) => {
        contactsMap[c.id] = c
      })
      setContacts(contactsMap)
    } catch (error) {
      console.error("Error loading contacts:", error)
    }
  }

  const loadOpportunities = async () => {
    try {
      const response = await opportunitiesAPI.getAll()
      const opportunitiesMap: Record<string, Opportunity> = {}
      response.data.forEach((o: any) => {
        opportunitiesMap[o.id] = o
      })
      setOpportunities(opportunitiesMap)
    } catch (error) {
      console.error("Error loading opportunities:", error)
    }
  }

  const handleSync = async () => {
    try {
      setSyncing(true)
      await emailsAPI.sync()
      await loadEmails()
    } catch (error: any) {
      console.error("Error syncing emails:", error)
      alert("Failed to sync emails: " + (error.response?.data?.error || error.message))
    } finally {
      setSyncing(false)
    }
  }

  const handleToggleRead = (emailId: number, e: React.MouseEvent) => {
    e.stopPropagation()
    if (isEmailRead(emailId)) {
      markEmailAsUnread(emailId)
    } else {
      markEmailAsRead(emailId)
    }
    // Force re-render
    setEmails([...emails])
  }

  const handleMarkAllAsRead = () => {
    markAllAsRead(emails.map((e) => e.id))
    setEmails([...emails])
  }

  const handleToggleAutosync = (enabled: boolean) => {
    setAutosyncEnabledState(enabled)
    setAutosyncEnabled(enabled)
  }

  const handleAutosyncIntervalChange = (value: string) => {
    const interval = parseInt(value, 10)
    setAutosyncIntervalState(interval)
    setAutosyncInterval(interval)
  }

  const handleLinkToOpportunity = (emailId: number) => {
    setLinkingEmailId(emailId)
    setShowLinkDialog(true)
  }

  const handleLinkEmail = async (opportunityId: string) => {
    if (!linkingEmailId) return

    try {
      await communicationsAPI.linkToOpportunity(linkingEmailId.toString(), opportunityId)
      await loadEmails()
      setShowLinkDialog(false)
      setLinkingEmailId(null)
    } catch (error: any) {
      console.error("Error linking email:", error)
      alert("Failed to link email: " + (error.response?.data?.error || error.message))
    }
  }

  const filteredEmails = emails.filter((email) => {
    // Search filter
    if (searchQuery) {
      const query = searchQuery.toLowerCase()
      const matchesSearch =
        email.subject?.toLowerCase().includes(query) ||
        email.body?.toLowerCase().includes(query) ||
        email.from_email?.toLowerCase().includes(query) ||
        email.to_email?.toLowerCase().includes(query) ||
        email.contact_name?.toLowerCase().includes(query)
      if (!matchesSearch) return false
    }

    // Unread filter
    if (filterUnread && isEmailRead(email.id)) {
      return false
    }

    return true
  })

  const unreadCount = getUnreadCount(emails.map((e) => e.id))

  // Group emails by conversation
  const groupedEmails = filteredEmails.reduce((acc, email) => {
    const key = email.conversation_id || email.id.toString()
    if (!acc[key]) {
      acc[key] = []
    }
    acc[key].push(email)
    return acc
  }, {} as Record<string, Email[]>)

  const conversationGroups = Object.values(groupedEmails).map((group) => ({
    conversationId: group[0].conversation_id,
    emails: group.sort((a, b) => new Date(b.occurred_at).getTime() - new Date(a.occurred_at).getTime()),
    latestEmail: group[0],
  }))

  const getEmailDirection = (email: Email) => {
    // This would need to be determined based on your user's email
    // For now, we'll use a simple heuristic
    return email.from_email.includes("@") ? "inbound" : "outbound"
  }

  return (
    <div className="min-h-screen bg-background">
      <Sidebar />
      <main className="ml-64">
        <Header title="Inbox" subtitle="View and manage your synced emails" />
        <div className="p-6">
          {/* Toolbar */}
          <Card className="mb-6">
            <CardContent className="pt-6">
              <div className="flex items-center justify-between gap-4">
                <div className="flex flex-1 items-center gap-4">
                  <div className="relative flex-1 max-w-md">
                    <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-muted-foreground" />
                    <Input
                      placeholder="Search emails..."
                      value={searchQuery}
                      onChange={(e) => setSearchQuery(e.target.value)}
                      className="pl-10"
                    />
                  </div>
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={handleSync}
                    disabled={syncing}
                  >
                    {syncing ? (
                      <>
                        <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                        Syncing...
                      </>
                    ) : (
                      <>
                        <RefreshCw className="mr-2 h-4 w-4" />
                        Sync
                      </>
                    )}
                  </Button>
                </div>
                <div className="flex items-center gap-2">
                  <Badge variant="outline">{filteredEmails.length} emails</Badge>
                </div>
              </div>
            </CardContent>
          </Card>

          <div className="grid gap-6 lg:grid-cols-3">
            {/* Email List */}
            <div className="lg:col-span-2">
              <Card>
                <CardHeader>
                  <CardTitle className="flex items-center gap-2">
                    <InboxIcon className="h-5 w-5" />
                    Emails
                  </CardTitle>
                </CardHeader>
                <CardContent>
                  {loading ? (
                    <div className="flex items-center justify-center py-12">
                      <Loader2 className="h-6 w-6 animate-spin text-muted-foreground" />
                    </div>
                  ) : conversationGroups.length === 0 ? (
                    <div className="py-12 text-center">
                      <Mail className="mx-auto h-12 w-12 text-muted-foreground mb-4" />
                      <p className="text-muted-foreground">No emails found</p>
                      <p className="text-sm text-muted-foreground mt-2">
                        {searchQuery ? "Try adjusting your search" : "Sync your Outlook account to get started"}
                      </p>
                    </div>
                  ) : (
                    <div className="space-y-2">
                      {conversationGroups.map((group, idx) => {
                        const email = group.latestEmail
                        const isSelected = selectedEmail?.id === email.id
                        const direction = getEmailDirection(email)
                        const contact = email.contact_id ? contacts[email.contact_id] : null
                        const opportunity = email.opportunity_id ? opportunities[email.opportunity_id] : null

                        return (
                          <div
                            key={email.id}
                            className={`cursor-pointer rounded-lg border p-4 transition-colors ${
                              isSelected
                                ? "border-primary bg-primary/5"
                                : "border-border bg-card hover:bg-secondary"
                            }`}
                            onClick={() => setSelectedEmail(email)}
                          >
                            <div className="flex items-start justify-between gap-4">
                              <div className="min-w-0 flex-1">
                                <div className="flex items-center gap-2 mb-1">
                                  <p className="font-medium text-foreground truncate">
                                    {contact?.name || email.from_email || "Unknown"}
                                  </p>
                                  {direction === "inbound" && (
                                    <Badge variant="outline" className="text-xs">
                                      Inbound
                                    </Badge>
                                  )}
                                  {direction === "outbound" && (
                                    <Badge variant="outline" className="text-xs">
                                      Outbound
                                    </Badge>
                                  )}
                                </div>
                                <p className="text-sm font-medium text-foreground mb-1 truncate">
                                  {email.subject || "(No Subject)"}
                                </p>
                                <p className="text-sm text-muted-foreground line-clamp-2">
                                  {email.body?.substring(0, 150) || email.body || "No preview available"}
                                </p>
                                <div className="flex items-center gap-4 mt-2">
                                  <span className="text-xs text-muted-foreground">
                                    {formatDistanceToNow(new Date(email.occurred_at), { addSuffix: true })}
                                  </span>
                                  {opportunity && (
                                    <Link
                                      href={`/opportunities/${email.opportunity_id}`}
                                      onClick={(e) => e.stopPropagation()}
                                      className="text-xs text-primary hover:underline"
                                    >
                                      Linked to {opportunity.title}
                                    </Link>
                                  )}
                                  {group.emails.length > 1 && (
                                    <Badge variant="outline" className="text-xs">
                                      {group.emails.length} messages
                                    </Badge>
                                  )}
                                </div>
                              </div>
                              <div className="flex items-center gap-2">
                                {email.deep_link && (
                                  <Button
                                    variant="ghost"
                                    size="icon"
                                    className="h-8 w-8"
                                    onClick={(e) => {
                                      e.stopPropagation()
                                      window.open(email.deep_link!, "_blank")
                                    }}
                                    title="Open in Outlook"
                                  >
                                    <ExternalLink className="h-4 w-4" />
                                  </Button>
                                )}
                              </div>
                            </div>
                          </div>
                        )
                      })}
                    </div>
                  )}
                </CardContent>
              </Card>
            </div>

            {/* Email Detail */}
            <div className="lg:col-span-1">
              {selectedEmail ? (
                <Card>
                  <CardHeader>
                    <div className="flex items-start justify-between">
                      <CardTitle className="text-lg">{selectedEmail.subject || "(No Subject)"}</CardTitle>
                      <div className="flex items-center gap-2">
                        {selectedEmail.deep_link && (
                          <Button
                            variant="ghost"
                            size="icon"
                            className="h-8 w-8"
                            onClick={() => window.open(selectedEmail.deep_link!, "_blank")}
                            title="Open in Outlook"
                          >
                            <ExternalLink className="h-4 w-4" />
                          </Button>
                        )}
                        {!selectedEmail.opportunity_id && (
                          <Button
                            variant="ghost"
                            size="icon"
                            className="h-8 w-8"
                            onClick={() => handleLinkToOpportunity(selectedEmail.id)}
                            title="Link to Opportunity"
                          >
                            <Link2 className="h-4 w-4" />
                          </Button>
                        )}
                      </div>
                    </div>
                  </CardHeader>
                  <CardContent className="space-y-4">
                    <div>
                      <p className="text-sm font-medium text-muted-foreground mb-1">From</p>
                      <p className="text-sm text-foreground">{selectedEmail.from_email}</p>
                    </div>
                    <div>
                      <p className="text-sm font-medium text-muted-foreground mb-1">To</p>
                      <p className="text-sm text-foreground">{selectedEmail.to_email}</p>
                    </div>
                    {selectedEmail.contact_id && contacts[selectedEmail.contact_id] && (
                      <div>
                        <p className="text-sm font-medium text-muted-foreground mb-1">Contact</p>
                        <Link
                          href={`/contacts`}
                          className="text-sm text-primary hover:underline"
                        >
                          {contacts[selectedEmail.contact_id].name}
                        </Link>
                      </div>
                    )}
                    {selectedEmail.opportunity_id && opportunities[selectedEmail.opportunity_id] && (
                      <div>
                        <p className="text-sm font-medium text-muted-foreground mb-1">Opportunity</p>
                        <Link
                          href={`/opportunities/${selectedEmail.opportunity_id}`}
                          className="text-sm text-primary hover:underline"
                        >
                          {opportunities[selectedEmail.opportunity_id].title}
                        </Link>
                      </div>
                    )}
                    <div>
                      <p className="text-sm font-medium text-muted-foreground mb-1">Date</p>
                      <p className="text-sm text-foreground">
                        {format(new Date(selectedEmail.occurred_at), "PPpp")}
                      </p>
                    </div>
                    {selectedEmail.conversation_id && (
                      <div>
                        <p className="text-sm font-medium text-muted-foreground mb-1">Conversation ID</p>
                        <p className="text-xs font-mono text-muted-foreground break-all">
                          {selectedEmail.conversation_id}
                        </p>
                      </div>
                    )}
                    <div className="pt-4 border-t">
                      <p className="text-sm font-medium text-muted-foreground mb-2">Message</p>
                      <div className="prose prose-sm max-w-none text-sm text-foreground whitespace-pre-wrap">
                        {selectedEmail.body || "No content available"}
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ) : (
                <Card>
                  <CardContent className="pt-6">
                    <div className="text-center py-12">
                      <Mail className="mx-auto h-12 w-12 text-muted-foreground mb-4" />
                      <p className="text-muted-foreground">Select an email to view details</p>
                    </div>
                  </CardContent>
                </Card>
              )}
            </div>
          </div>

          {/* Link to Opportunity Dialog */}
          {showLinkDialog && (
            <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50">
              <Card className="w-full max-w-md">
                <CardHeader>
                  <CardTitle>Link Email to Opportunity</CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  <p className="text-sm text-muted-foreground">
                    Select an opportunity to link this email to:
                  </p>
                  <div className="space-y-2 max-h-60 overflow-y-auto">
                    {Object.values(opportunities).map((opp) => (
                      <Button
                        key={opp.id}
                        variant="outline"
                        className="w-full justify-start"
                        onClick={() => handleLinkEmail(opp.id)}
                      >
                        {opp.title}
                      </Button>
                    ))}
                  </div>
                  <div className="flex justify-end gap-2 pt-4">
                    <Button variant="outline" onClick={() => setShowLinkDialog(false)}>
                      Cancel
                    </Button>
                  </div>
                </CardContent>
              </Card>
            </div>
          )}
        </div>
      </main>
    </div>
  )
}

